import React from 'react'
import { Link } from 'gatsby'
import styled from 'styled-components'

const menu = [
  ['chairman', ' () -  '],
  ['', ''],
  ['news', ''],
  ['nikah', ''],
  ['salaah-for-women', '  '],
  ['basics-of-islam', ' '],
  ['quran-and-its-sciences', '   '],
  ['allowed-and-forbidden-in-islam', '    '],
  ['dua', ' -  '],
  ['paradise', '  ']
]

const Menu = styled(props => (
  <nav {...props}>
    <ul>
      {menu.map(([path, label]) => (
        <li>
          <Link to={path}>{label}</Link>
        </li>
      ))}
    </ul>
  </nav>
))`
  background: #314b4a;
  border-radius: 5px;

  ul {
    list-style: none;
    padding: 0.5em;

    li {
      display: inline-block;
      padding: 0.5em 1em;
    }
  }

  a {
    color: #f5f5f5;
    text-decoration: none;

    :hover {
      color: #cb6a2d;
    }
  }
`

Menu.displayName = 'Menu'

export default Menu
